.search-room {
    min-height: calc(100vh - 620px);
}

.search-room-box {
    display: flex;
    .search-room__filter {
        margin-top: 30px;
        width: 266px;
        margin-right: 60px;
        *, *:before, *:after {
            -webkit-border-sizing: border-box;
            -moz-border-sizing: border-box;
            box-sizing: border-box;
        }
    }
    .search-room__room-list {
        margin-top: 11px;
        margin-bottom: 40px;
        .search-room__room-list-text {
            font-size: 24px;
            font-family: 'Open Sans';
            color: #1F2041;
        }
    }
    .search-room-room-list__rooms {
        display: grid;
        grid-template-rows: repeat(3, 1fr);
        grid-template-columns: repeat(3, 1fr);
        column-gap: 0.78rem;
        row-gap: 1.35rem;
    }
}


.pagination-box {
    padding-left: 310px;
    .pagination {
        margin: auto;
        margin-bottom: 75px;
    }
    
}


@media(max-width: 1240px) {
    .search-room {
        .search-room__filter {
            margin-right: 1.5vw;
        }
        .search-room-room-list__rooms {
            grid-template-rows: repeat(6, 1fr);
            grid-template-columns: repeat(2, 1fr);
            grid-gap: 0.8rem;
        }
    }
    .number-rate-container {
        width: 100%;
        height: 243px;
        .number-rate__photo-slider {
            width: 100%;
            margin-bottom: 0;
        }
        .number-rate__img {
            width: 100%;
        }
        .number-rate__name-price-rating-reviews {
            width: 100%;
            .number-rate__line {
                width: 100%;
            }
        }
    }
    .search-room__room-list-text {
        width: 50vw;
    }
    .search-room__room-list {
        min-width: 75%;
    }
}

@media(max-width: 1052px) {
    .search-room__room-list {
        min-width: 74%;
    }
}
@media(max-width: 1000px) {
    .search-room__room-list {
